.geniusify{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
}

.geniusify__body{
    background-color: rgba(255, 255, 255,0.2);
    width: 70%;
    margin: 1rem;
    padding: 1rem;
    border-radius: 2rem;
}

.geniusify h1{
    color: white;
    margin-top: 1rem;
    margin-bottom: 0;
    letter-spacing: 1.3vw;
    font-size: 4vw;
}

.geniusify-textarea__wrapper{
    margin: 5vh;
}

.geniusify-textarea{
    width: 60%;
    max-width: 600px;
    height: 10rem;
    text-align: left;
    box-sizing: border-box;
    resize: none;
    border-radius: 3px;
    border: none;
    outline: inherit;
}

.geniusify-textarea:focus{
    outline: none;
}

.geniusify-button{
    border: none;
    width: 8rem;
    height: 2.4rem;
    border-radius: 3px;
    color: #04afff;
    font-weight: 800;
}

.geniusify-button:hover,:focus{
    background-color: white;
    outline: none;
}